:root{
  --primary-color: #e0c521;
}

.btn {
  padding: 0.6rem 0.8rem;
  font-size: 1.2rem;
  text-align: center;
  border: 0;
  outline: none;
  border-radius: 10px;
  width: 120px;
  margin-left: 10px;
  box-shadow: 1px -3px 52px -5px rgba(151, 122, 175, 0.73);
  cursor: pointer;
}

.btn:active{
  transform: scale(0.98);
  background-color: #8b7329;
  color: #383838;
}

.btn.home {
  background-color: #848689;
}
.btn.swap {
  background-color: #848689;
}
.btn.games {
  background-color: #848689;
}
