.head{
  display: grid;
  width: 200px;
  height: 200px;
  margin: 10px auto;
  align-items: center;

  
}

.head h1{
  text-align: center;
  color: white;
  object-fit: cover;
}

.head img{
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.head-info{
  display: grid;
  margin: 0;
  
}