:root{
  --primary-color: #e0c521
}

html, body {
  height: 100%;
  margin: 0;
  overflow: hidden; /* Prevent scrolling */
}

.black-box{
  display: grid;
  background: rgb(28,2,2);
  background: -moz-radial-gradient(circle, rgba(28,2,2,1) 0%, rgb(191, 191, 191) 0%, rgba(0,0,0,1) 52%);
  background: -webkit-radial-gradient(circle, rgba(28,2,2,1) 0%, rgb(235, 235, 235) 0%, rgba(0,0,0,1) 52%);
  background: radial-gradient(circle, rgba(28,2,2,1) 0%, rgb(45, 66, 77) 0%, rgba(0,0,0,1) 52%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#1c0202",endColorstr="#000000",GradientType=1);
  height: 100dvh;
  align-items: center;
  justify-content: center;
  z-index: -2;

}

.under-bar{
  position: relative;
  display: flex;
  background-color: #131313f7;
  width: 100dvw;
  height: 10dvh;
  padding: 8px;
  top: 8dvh;
  margin: 0% auto auto auto;
  align-items: center;
  justify-content: center;
  z-index: 1;
  
}


.tutorial{
  display: none;
}

